#contact {
  height: 50vh;
  text-align: center;
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-template-rows: 50% 50%;
}

.final {
  grid-column-start: 1;
  grid-column-end: 3;
  align-self: end;
  font-size: 2em;
}

.info {
  grid-column-start: 2;
  grid-column-end: 4;
}

.box {
  border: 1px solid red;
}